
header {
	position: relative; }
nav {
	height: 50px;
	background-color: #212121; }
.hamburger {
	displey: none; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style: none;
	&_link {
		text-decoration: none;
		color: $white_text_color;
		font-size: 15px;
		font-weight: 300; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 40px;
		&:last-child {
			border-right: none; } } }
.subheader {
	position: absolute;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, .6);
	z-index: 1;
	&_link {
		display: block;
		margin-top: 16px;
		max-width: 100%; }
	&_official {
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400;
		margin-top: 6px; }
	&_phone {
		display: block;
		color: rgb(255, 255, 255);
		font-size: 22px;
		font-weight: 400;
		letter-spacing: 0px;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		text-align: right; }
	&_btn {
		width: 172px;
		height: 46px;
		box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.12);
		background-image: linear-gradient(to top,#1eacc7 0%,#4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: $white_text_color;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		border: none;
		margin-top: 18px;
		border-radius: 4px;
		@include btn_hover; }
	&_call {
		display: block;
		color: rgb(255, 255, 255);
		font-size: 12px;
		font-weight: 300;
		line-height: 14px;
		letter-spacing: 0px;
		text-align: right;
		margin-top: 21px; } }
.promo {
	min-height: 800px;
	background: url('../img/bg/uber_bg.png')center center/cover no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: $white_text_color;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		text-align: center;
		line-height: 60px; }
	&_subheader {
		color: $white_text_color;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		text-align: center;
		line-height: 36px;
		margin-top: 22px; }
	&_descr {
		color: $white_text_color;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top,#1eacc7 0%,#4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0,0,0,0.21);
		color: $white_text_color;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }
.reasons {
	min-height: 849px;
	padding: 30px 0 0 0;
	.label {
		width: 115px;
		height: 27px;
		background-color: rgb(0, 0, 0);
		color: rgb(255, 255, 255);
		font-size: 11px;
		font-weight: 400;
		line-height: 11px;
		text-align: center;
		text-transform: uppercase;
		margin: 0 auto;
		padding: 7px 12px; }
	.titel {
		color: rgb(34, 34, 34);
		font-size: 38px;
		font-weight: 700;
		line-height: 48px;
		letter-spacing: 0px;
		text-align: center;
		text-transform: uppercase;
		margin-top: 33px; }
	&_block {
		display: flex;
		justify-content: center;
		align-items: center; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		min-width: 116px;
		min-height: 116px;
		background: rgb(30, 172, 199);
		border-radius: 100%;
		margin-top: 55px;
		margin-bottom: 35px; }
	&_descr {
		width: 371px;
		margin-top: 20px; }
	&_subtitle {
		font-size: 17px;
		font-weight: 400;
		letter-spacing: 0px;
		text-align: left;
		color: $blue; }
	&_text {
		font-size: 15px;
		font-weight: 300;
		line-height: 21px;
		letter-spacing: 0px;
		text-align: left; } }

.label {
	width: 115px;
