.mobile {
	min-height: 864px;
	padding: 48px 0 77px 0;
	background: url('../img/bg/mobile_bg.png') center (center/cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/card1.png') center (center/cover) no-repeat; }
		&_2 {
			background: url('../img/card2.png') center (center/cover) no-repeat; }
		&_3 {
			background: url('../img/card3.png') center (center/cover) no-repeat; }
		&_4 {
			background: url('../img/card4.png') center (center/cover) no-repeat; }
		&_5 {
			background: url('../img/card5.png') center (center/cover) no-repeat; }
		&_6 {
			background: url('../img/card6.png') center (center/cover) no-repeat; }
		&_subtitle {
			color: rgb(255, 255, 255);
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer; } } }

.label_second {
	width: 115px;
	height: 27px;
	background: rgb(255, 255, 255);
	color: rgb(7, 7, 22);
	font-size: 11px;
	font-weight: 400;
	line-height: 11px;
	text-align: center;
	text-transform: uppercase;
	margin: 0 auto;
	padding: 7px 12px; }
.title_second {
	color: rgb(255, 255, 255);
	font-size: 38px;
