.ubercar {
	min-height: 896px;
	background: url('../img/bg/mobilesecond_bg.png') center (center/cover) no-repeat;
	padding: 59px 0 138px 0;
	&_img {
		display: flex;
		justify-content: center;
		margin-top: 54px; }
	&_descr {
		&_text {
			text-align: center;
			color: rgb(34, 34, 34);
			font-size: 18px;
			font-weight: 300;
			line-height: 24px; } } }

.require {
	padding: 65px 0 87px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: rgb(28, 168, 195);
		font-size: 22px;
		font-weight: 400;
		line-height: 24px; }

	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 21px;

		li {
			position: relative;
			color: rgb(34, 34, 34);
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&::before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue1.png') center (center/cover) no-repeat; } } }
	.warning {
		.require_title {
			color: $red; }
		.require_list {
			li {
				&::before {
				    background: url('../icons/check_red1.png') center (center/cover) no-repeat; } } } } }

.map {
	min-height: 720px;
	background: url('../img/bg/map_bg.png')	center (center/cover) no-repeat;
	padding: 48px 0 22px 0;
	&_button {
		margin: 0 auto;
		margin-top: 457px;
		color: rgb(31, 186, 214);
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		text-align: center;
		width: 223px;
		height: 68px;
		background-color: black;
		cursor: pointer;
		a {
			text-decoration: none;
			color: rgb(31, 186, 214); } } }
.end {
	margin: 12px 0 0 0;
	position: relative;
	&_line {
		margin: 0 auto;
		width: 301px;
		height: 7px;
		background: rgb(31, 186, 214); }
	&_social {
		display: inline-block;
		position: absolute;
		top: 50%;
		margin-left: 23px;
		&_logo {
			padding: 10px;
			margin: 0 4px;
			min-width: 40px;
			min-height: 40px;
			background-color: black;
			border-radius: 100%;
			text-align: center;
			color: rgb(255, 255, 255);
			span {
				display: inline-block;
				height: 9px;
				width: 18px; } } }
	&_link {
		display: inline-block;
		position: absolute;
		top: 50%;
		right: 20px; }
	&_block {
		margin-top: 15px;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		justify-content: center;
		list-style-type: none;
		ul {
			list-style-type: none;
			display: flex;
			a {
				text-decoration: none; }
			li {
				color: rgb(7, 7, 22);
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				margin: 0 46px 0 0; } } }
	&_block2 {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		justify-content: center;
		top: 50px;
		ul {
			list-style-type: none;
			display: flex;
			a {
				text-decoration: none; }
			li {
				color: rgb(153, 153, 153);
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase;
				margin: 0 22px 0 0; } } }
	&_lang {
		display: block;
		margin: 0 auto;
		color: #1fbad6;
		font-size: 11px;
		font-weight: 400;
		margin-top: 19px;
		text-transform: uppercase;
		text-align: center;
		margin-top: 70px; } }
.xs-visible {
	display: none; }

