@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 24px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .mobile {
        &_item {
            width: 300px; } }
    .end {
        &_link {
            img {
                width: 250px; } } }
    .ubercar {
        &_img {
            img {
                width: 700px; } } }
    .end {
        &_social {
            &_logo {
                padding: 6px; } } } }

@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 10px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_link {
            img {
                width: 100%; } }
        &_official {
            font-size: 10px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            width: 100%;
            height: 40px;
            font-size: 12px;
            margin-top: 21px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_block {
            flex-direction: column; }
        &_round {
            margin-top: 8px;
            margin-bottom: 30px; }
        &_descr {
            width: 365px;
            margin-top: 0; }
        .titel {
            font-size: 30px; } }
    .mobile {
        &_item {
            width: 220px; } }
    .end {
        &_block {
            ul {
                li {
                    font-size: 12px;
                    margin: 0 30px 0 0; } } } } }

@media (max-width: 767px) {
    .ubercar {
        &_img {
            img {
                width: 525px; } } }
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        right: 0;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8 );
        transition: 0.6s all;
        z-index: 2;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 9px;
            color: rgb(255, 255, 255);
            font-size: 14px;
            font-weight: 400;
            text-align: center;
            text-transform: uppercase; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 4px; } }
    .promo {
        &_header {
            line-height: 45px; } }
    .mobile {
        &_item {
            width: 320px; } } }
@media (max-width: 575px) {
    .subheader {
        height: 60px;
        &_official {
            display: none; }
        &_link {
            margin-top: 16px; }
        &_phone {
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 8px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 4px; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 10px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 189px;
            height: 50px;
            font-size: 16px;
            margin-top: 73px; } }
    .menu {
        width: 65%; }
    .reasons {
        padding: 23px 0 38px 0;
        .titel {
            font-size: 16px;
            margin-top: 16px; }
        &_round {
            min-width: 100px;
            min-height: 100px; }
        &_descr {
            width: 280px; } }
    .mobile {
        padding: 28px 0 42px 0;
        .title {
            font-size: 16px;
            margin-top: 14px; }
        .subtitle {
            font-size: 14px; } }
    .ubercar {
        height: 393px;
        padding: 27px 0 30px 0;
        &_descr {
            &_text {
                color: #fff; } }
        .title_second {
            display: none; }
        .subtitle_second {
            display: none; }
        &_img {
            img {
                width: 300px; } } }
    .require {
        padding: 24px 0 30px;
        .title {
            font-size: 16px;
            margin-top: 14px; }
        &_block {
            margin: 30px 0; }
        &_list {
            li {
                line-height: 18px; } } }
    .map {
        min-height: 360px;
        padding: 26px 0 30px 0;
        .title_second {
            font-size: 16px;
            margin-top: 14px; }
        .subtitle_second {
            font-size: 14px; }
        &_button {
            margin-top: 211px; } }
    .end {} }

